html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.header-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 5rem;
  background-color: #dfe7ec;
  font-family: "CamingoDosProCd-SemiBold";
}
.header-top p {
  font-size: 1.5rem;
  color: #747474;
}

.header-phone {
  display: flex;
  align-items: center;
}
.header-phone > * {
  margin: 1rem;
}
.header-phone .img-container {
  height: 2rem;
  padding-left: 1rem;
}
@media (min-width: 769px) {
  .header-phone .img-container {
    padding-left: 3rem;
  }
}
.header-phone .img-container img {
  height: 100%;
}

.header-social-media {
  display: flex;
  align-items: center;
}
.header-social-media .img-container {
  height: 2rem;
  margin: 1.5rem;
}
@media (min-width: 769px) {
  .header-social-media .img-container {
    margin: 3rem;
  }
}
.header-social-media .img-container:last-child {
  padding-right: 1rem;
}
@media (min-width: 769px) {
  .header-social-media .img-container:last-child {
    padding-right: 2rem;
  }
}
.header-social-media .img-container img {
  height: 100%;
}

.header-middle {
  height: 15rem;
  background-color: #1A9BC4;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 769px) {
  .header-middle {
    height: 20rem;
  }
}
.header-middle .img-container {
  height: 7.5rem;
}
@media (min-width: 769px) {
  .header-middle .img-container {
    height: 12.5rem;
  }
}
.header-middle .img-container img {
  height: 100%;
}

.header-bottom {
  height: auto;
  background-color: #dfe7ec;
  display: flex;
  align-items: center;
}
@media (min-width: 769px) {
  .header-bottom {
    height: 7rem;
  }
}
@media (min-width: 1025px) {
  .header-bottom {
    height: 10rem;
  }
}
.header-bottom .navbar {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
@media (min-width: 769px) {
  .header-bottom .navbar {
    flex-direction: row;
  }
}
.header-bottom .navbar .navbar-element {
  display: flex;
  align-items: center;
}
.header-bottom .navbar .navbar-element .img-container {
  height: 2rem;
}
@media (min-width: 769px) {
  .header-bottom .navbar .navbar-element .img-container {
    height: 2rem;
  }
}
@media (min-width: 1025px) {
  .header-bottom .navbar .navbar-element .img-container {
    height: 3rem;
  }
}
.header-bottom .navbar .navbar-element .img-container img {
  height: 100%;
}
.header-bottom .navbar .navbar-element p {
  font-family: "CodeProBoldLC";
  font-size: 1.25rem;
  margin: 1.35rem;
}
@media (min-width: 769px) {
  .header-bottom .navbar .navbar-element p {
    font-size: 1.5rem;
    margin: 1rem;
  }
}
@media (min-width: 1025px) {
  .header-bottom .navbar .navbar-element p {
    font-size: 1.75rem;
    margin: 2rem;
  }
}
.header-bottom .navbar a {
  text-decoration: none;
  color: #747474;
}
.header-bottom .navbar a:hover {
  border-bottom: 1px solid #1A9BC4;
  border-top: 1px solid transparent;
  transition: border-bottom 0.5s ease-in-out;
}

.main-hero .hero-text {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Gotham-Bold";
  color: #1A9BC4;
  font-size: 2rem;
  margin: auto;
}
.main-hero .hero-text h2 {
  margin: 6rem 4rem;
  text-align: center;
}
@media (min-width: 769px) {
  .main-hero .hero-text h2 {
    margin-top: 15rem;
    margin-bottom: 7rem;
    font-size: 2.5rem;
  }
}
@media (min-width: 1025px) {
  .main-hero .hero-text h2 {
    margin: 20rem 15rem 10rem 15rem;
    font-size: 2.5rem;
  }
}
@media (min-width: 1201px) {
  .main-hero .hero-text h2 {
    margin-top: 30rem;
    margin-bottom: 10rem;
    margin: 30rem 35rem 10rem 35rem;
    font-size: 3.5rem;
  }
}
.main-hero .img-container {
  margin-top: -2rem;
  margin-bottom: 7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 8rem;
}
@media (min-width: 769px) {
  .main-hero .img-container {
    height: 12rem;
    margin-top: 0rem;
    margin-bottom: 15rem;
  }
}
@media (min-width: 1025px) {
  .main-hero .img-container {
    height: 15rem;
    margin-bottom: 15rem;
  }
}
@media (min-width: 1201px) {
  .main-hero .img-container {
    margin-bottom: 25rem;
  }
}
.main-hero .img-container img {
  height: 100%;
}

.main-bg-image {
  position: absolute;
  top: 43.7rem;
  z-index: -1;
}
.main-bg-image .img-container {
  width: 100%;
}
.main-bg-image .img-container img {
  width: 100%;
}
@media (min-width: 769px) {
  .main-bg-image {
    top: 32rem;
  }
}
@media (min-width: 1025px) {
  .main-bg-image {
    top: 35rem;
  }
}

.main-services {
  display: grid;
  grid-template-columns: 1fr;
  margin: 5rem;
}
@media (min-width: 769px) {
  .main-services {
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    margin: 2rem;
    margin-bottom: 10rem;
  }
}
@media (min-width: 1025px) {
  .main-services {
    gap: 4rem;
    margin: 5rem;
    margin-bottom: 10rem;
  }
}
@media (min-width: 1201px) {
  .main-services {
    gap: 5rem;
    margin: 8rem;
  }
}
.main-services .service {
  width: 100%;
  height: auto;
  border-radius: 30rem 30rem 0 0;
  background-color: #deebef;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  margin-bottom: 5rem;
  position: relative;
}
@media (min-width: 769px) {
  .main-services .service {
    height: 47rem;
  }
}
@media (min-width: 1025px) {
  .main-services .service {
    height: 60rem;
  }
}
@media (min-width: 1201px) {
  .main-services .service {
    height: 65rem;
  }
}
@media (min-width: 769px) {
  .main-services .service {
    margin-bottom: auto;
  }
}
.main-services .service .img-container {
  height: 6rem;
  margin-top: 7rem;
  margin-bottom: 0;
}
@media (min-width: 769px) {
  .main-services .service .img-container {
    margin-top: 10rem;
    margin-bottom: 5rem;
  }
}
@media (min-width: 1025px) {
  .main-services .service .img-container {
    height: 8rem;
  }
}
.main-services .service .img-container img {
  height: 100%;
}
.main-services .service .service-text {
  font-family: "BaseticaPro-Med";
  margin: 4rem 4rem 8rem 4rem;
  text-align: center;
  font-size: 1.5rem;
  color: #747474;
}
@media (min-width: 769px) {
  .main-services .service .service-text {
    font-size: 1.5rem;
    margin: 1.5rem 4rem;
  }
}
@media (min-width: 1025px) {
  .main-services .service .service-text {
    font-size: 2rem;
    margin: 2rem 6rem;
  }
}
@media (min-width: 1201px) {
  .main-services .service .service-text {
    font-size: 2rem;
    margin: 2rem 8rem;
  }
}
.main-services .service button {
  border: none;
}
.main-services .service .service-btn {
  position: absolute;
  bottom: -2.25rem;
  font-family: "Gotham-Bold";
  font-size: 1.5rem;
  background-color: #2ba7dd;
  color: #ffffff;
  text-align: center;
  border-radius: 0 0 3rem 3rem;
  margin: auto 5rem;
  width: 100%;
  padding: 1.5rem 0;
}
@media (min-width: 769px) {
  .main-services .service .service-btn {
    width: 80%;
    font-size: 1.75rem;
    border-radius: 3rem;
  }
}
@media (min-width: 1025px) {
  .main-services .service .service-btn {
    font-size: 2.5rem;
    border-radius: 3rem;
  }
}
.main-services .service .service-btn-odd {
  background-color: #ffccd9;
  color: #e26d93;
}

.main-description {
  display: grid;
  grid-template-columns: 1fr;
  margin: 4rem;
  gap: 5rem;
}
@media (min-width: 1025px) {
  .main-description {
    margin: 15rem;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.main-description .description-image {
  width: 100%;
}
.main-description .description-image .img-container {
  width: 100%;
}
.main-description .description-image .img-container img {
  width: 100%;
}
.main-description .description-text h3 {
  color: #1A9BC4;
  font-family: "Gotham-Bold";
  font-size: 2rem;
  text-align: center;
  margin-bottom: 3rem;
}
@media (min-width: 769px) {
  .main-description .description-text h3 {
    font-size: 2.5rem;
  }
}
@media (min-width: 1201px) {
  .main-description .description-text h3 {
    font-size: 3rem;
  }
}
.main-description .description-text p {
  font-family: "BaseticaPro-Med";
  color: #747474;
  font-size: 1.5rem;
  text-align: center;
}
@media (min-width: 769px) {
  .main-description .description-text p {
    font-size: 1.6rem;
  }
}
@media (min-width: 1201px) {
  .main-description .description-text p {
    font-size: 1.75rem;
  }
}

.grid-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.main-grid {
  width: 75%;
  display: grid;
  grid-template-columns: 1fr;
}
@media (min-width: 769px) {
  .main-grid {
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(5, 1fr);
    gap: 2rem;
    width: 90%;
  }
}
.main-grid > * {
  color: #ffffff;
  font-family: "BaseticaPro-Med";
  font-size: 2rem;
  text-align: center;
}
.main-grid .img-container {
  height: 6rem;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}
@media (min-width: 769px) {
  .main-grid .img-container {
    height: 10rem;
    margin-top: 3rem;
    margin-bottom: 2.5rem;
  }
}
.main-grid .img-container img {
  height: 100%;
}

.grid-1 {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1a92bb;
  border-radius: 4rem 4rem 0 0;
}
@media (min-width: 769px) {
  .grid-1 {
    grid-area: 1/1/2/5;
    border-radius: 4rem 0 0 0;
  }
}

.grid-2 {
  background-color: #1a92bb;
}
@media (min-width: 769px) {
  .grid-2 {
    grid-area: 2/1/4/5;
  }
}

.grid-3 {
  background-color: #1a90bbb0;
}
@media (min-width: 769px) {
  .grid-3 {
    grid-area: 1/5/4/9;
  }
}

.grid-4 {
  background-color: #1a92bb;
}
@media (min-width: 769px) {
  .grid-4 {
    grid-area: 1/9/4/13;
  }
}

.grid-5 {
  background-color: #1a90bbb0;
}
@media (min-width: 769px) {
  .grid-5 {
    grid-area: 4/1/6/4;
  }
}

.grid-6 {
  background-color: #1a92bb;
}
@media (min-width: 769px) {
  .grid-6 {
    grid-area: 4/4/6/10;
  }
}

.grid-7 {
  background-color: #1a90bbb0;
  border-radius: 0 0 4rem 4rem;
}
@media (min-width: 769px) {
  .grid-7 {
    grid-area: 4/10/6/13;
    border-radius: 0 0 0 0;
  }
}

.grid-1,
.grid-2,
.grid-3,
.grid-4,
.grid-5,
.grid-6,
.grid-7 {
  display: flex;
  padding: 1.5rem 0 1.5rem 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.grid-1 {
  padding-bottom: 0;
}

.main-form {
  margin-top: 5rem;
}
@media (min-width: 769px) {
  .main-form {
    margin: 4rem;
  }
}
@media (min-width: 1025px) {
  .main-form {
    margin: 6rem;
  }
}
@media (min-width: 1201px) {
  .main-form {
    margin: 6rem;
  }
}

.main-form-header {
  background-color: #1A9BC4;
  border-radius: none;
  height: auto;
}
@media (min-width: 769px) {
  .main-form-header {
    border-radius: 0 0 0 10rem;
    display: grid;
    grid-template-columns: 1fr;
  }
}
@media (min-width: 1025px) {
  .main-form-header {
    height: 40rem;
    border-radius: 0 0 0 10rem;
    grid-template-columns: 4fr 5fr;
  }
}
@media (min-width: 1201px) {
  .main-form-header {
    height: 40rem;
    border-radius: 0 0 0 10rem;
    grid-template-columns: 1fr 2fr;
  }
}

.form-title {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  font-size: 1.75rem;
  text-align: center;
  padding: 3rem 4.5rem;
  font-family: "Gotham-Bold";
}
@media (min-width: 769px) {
  .form-title {
    font-size: 3rem;
    text-align: start;
    margin-left: 7rem;
  }
}

.main-form-body {
  display: grid;
}
@media (min-width: 1025px) {
  .main-form-body {
    grid-template-columns: 2fr 3fr;
  }
}
.main-form-body .form-description {
  display: flex;
  flex-direction: column;
}
@media (min-width: 769px) {
  .main-form-body .form-description {
    margin: 5rem 12rem 0 12rem;
  }
}
@media (min-width: 1025px) {
  .main-form-body .form-description {
    margin: 5rem 5rem 5rem 5rem;
  }
}
@media (min-width: 1201px) {
  .main-form-body .form-description {
    margin: 10rem 7.5rem 10rem 7.5rem;
  }
}
.main-form-body .form-description .form-info {
  display: flex;
  align-items: center;
  margin: 2rem;
}
.main-form-body .form-description .form-info .img-container {
  display: flex;
  justify-content: center;
  width: 4rem;
  padding: 0.5rem;
}
@media (min-width: 769px) {
  .main-form-body .form-description .form-info .img-container {
    width: 4.5rem;
  }
