@use "base/variables" as v;
@use "base/mixins" as m;

.main-description {
  display: grid;
  grid-template-columns: 1fr;
  margin: 4rem;
  gap: 5rem;
  @include m.desktop {
    margin: 15rem;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
  .description-image {
    width: 100%;
    .img-container {
      width: 100%;
      img {
        width: 100%;
      }
    }
  }
  .description-text {
    h3 {
      color: v.$primaryBlueDark;
      font-family: v.$title;
      font-size: 2rem;
      @include m.laptop {
        font-size: 2.5rem;
      }
      @include m.desktopXL {
        font-size: 3rem;
      }
      text-align: center;
      margin-bottom: 3rem;
    }
    p {
      font-family: v.$text;
      color: v.$secondaryGray;
      font-size: 1.5rem;
      @include m.laptop {
        font-size: 1.6rem;
      }
      @include m.desktopXL {
        font-size: 1.75rem;
      }
      text-align: center;
    }
  }
}
