@use "base/variables" as v;
@use "base/mixins" as m;

.main-hero {
  .hero-text {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: v.$title;
    color: v.$primaryBlueDark;
    font-size: 2rem;
    margin: auto;
    h2 {
      margin: 6rem 4rem;
      @include m.laptop {
        margin-top: 15rem;
        margin-bottom: 7rem;
        font-size: 2.5rem;
      }
      @include m.desktop {
        margin: 20rem 15rem 10rem 15rem;
        font-size: 2.5rem;
      }
      @include m.desktopXL {
        margin-top: 30rem;
        margin-bottom: 10rem;
        margin: 30rem 35rem 10rem 35rem;
        font-size: 3.5rem;
      }
      text-align: center;
    }
  }
  .img-container {
    margin-top: -2rem;
    margin-bottom: 7rem;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 8rem;
    @include m.laptop {
      height: 12rem;
      margin-top: 0rem;
      margin-bottom: 15rem;
    }
    @include m.desktop {
      height: 15rem;
      margin-bottom: 15rem;
    }
    @include m.desktopXL {
      margin-bottom: 25rem;
    }
    img {
      height: 100%;
    }
  }
}

.main-bg-image {
  position: absolute;
  top: 43.7rem;
  z-index: -1;
  .img-container {
    width: 100%;
    img {
      width: 100%;
    }
  }
  @include m.laptop {
    top: 32rem;
  }
  @include m.desktop {
    top: 35rem;
  }
}
