@use "base/variables" as v;
@use "base/mixins" as m;

.header-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 5rem;
  background-color: v.$grayBackground;
  font-family: v.$textNarrow;
  p {
    font-size: 1.5rem;
    color: v.$secondaryGray;
  }
}

.header-phone {
  display: flex;
  align-items: center;
  & > * {
    margin: 1rem;
  }
  .img-container {
    height: 2rem;
    padding-left: 1rem;
    @include m.laptop {
      padding-left: 3rem;
    }
    img {
      height: 100%;
    }
  }
}

.header-social-media {
  display: flex;
  align-items: center;
  .img-container {
    height: 2rem;
    margin: 1.5rem;
    @include m.laptop {
      margin: 3rem;
    }
    &:last-child {
      padding-right: 1rem;
      @include m.laptop {
        padding-right: 2rem;
      }
    }
    img {
      height: 100%;
    }
  }
}

.header-middle {
  height: 15rem;
  @include m.laptop {
    height: 20rem;
  }
  background-color: v.$primaryBlueDark;
  display: flex;
  align-items: center;
  justify-content: center;
  .img-container {
    height: 7.5rem;
    @include m.laptop {
      height: 12.5rem;
    }
    img {
      height: 100%;
    }
  }
}

.header-bottom {
  height: auto;
  @include m.laptop {
    height: 7rem;
  }
  @include m.desktop {
    height: 10rem;
  }
  background-color: v.$grayBackground;
  display: flex;
  align-items: center;
  .navbar {
    width: 100%;
    display: flex;
    flex-direction: column;
    @include m.laptop {
      flex-direction: row;
    }
    justify-content: space-around;
    align-items: center;
    .navbar-element {
      display: flex;
      align-items: center;
      .img-container {
        height: 2rem;
        @include m.laptop {
          height: 2rem;
        }
        @include m.desktop {
          height: 3rem;
        }
        img {
          height: 100%;
        }
      }
      p {
        font-family: v.$titleBold;
        font-size: 1.25rem;
        margin: 1.35rem;
        @include m.laptop {
          font-size: 1.5rem;
          margin: 1rem;
        }
        @include m.desktop {
          font-size: 1.75rem;
          margin: 2rem;
        }
      }
    }
    a {
      text-decoration: none;
      color: v.$secondaryGray;
      &:hover {
        border-bottom: 1px solid v.$primaryBlueDark;
        border-top: 1px solid transparent;
        transition: border-bottom 0.5s ease-in-out;
      }
    }
  }
}
