@use "base/variables" as v;
@use "base/mixins" as m;

.grid-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.main-grid {
  width: 75%;
  display: grid;
  grid-template-columns: 1fr;
  @include m.laptop {
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(5, 1fr);
    gap: 2rem;
    width: 90%;
  }
  & > * {
    color: v.$white;
    font-family: v.$text;
    font-size: 2rem;
    text-align: center;
  }
  .img-container {
    height: 6rem;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    @include m.laptop {
      height: 10rem;
      margin-top: 3rem;
      margin-bottom: 2.5rem;
    }
    img {
      height: 100%;
    }
  }
}

.grid-1 {
  @include m.laptop {
    grid-area: 1 / 1 / 2 / 5;
    border-radius: 4rem 0 0 0;
  }
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: v.$blueGridDark;
  border-radius: 4rem 4rem 0 0;
}
.grid-2 {
  @include m.laptop {
    grid-area: 2 / 1 / 4 / 5;
  }
  background-color: v.$blueGridDark;
}
.grid-3 {
  @include m.laptop {
    grid-area: 1 / 5 / 4 / 9;
  }
  background-color: v.$blueGrid;
}
.grid-4 {
  @include m.laptop {
    grid-area: 1 / 9 / 4 / 13;
  }
  background-color: v.$blueGridDark;
}
.grid-5 {
  @include m.laptop {
    grid-area: 4 / 1 / 6 / 4;
  }
  background-color: v.$blueGrid;
}
.grid-6 {
  @include m.laptop {
    grid-area: 4 / 4 / 6 / 10;
  }
  background-color: v.$blueGridDark;
}
.grid-7 {
  @include m.laptop {
    grid-area: 4 / 10 / 6 / 13;
    border-radius: 0 0 0 0;
  }
  background-color: v.$blueGrid;
  border-radius: 0 0 4rem 4rem;
}

.grid-1,
.grid-2,
.grid-3,
.grid-4,
.grid-5,
.grid-6,
.grid-7 {
  display: flex;
  padding: 1.5rem 0 1.5rem 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.grid-1 {
  padding-bottom: 0;
}
